import React from "react";
import { BrowserRouter as Router,Route,Switch } from "react-router-dom";


import Home from "./pages/Home.js";
import Aboutus from "./pages/Aboutus.js";
import Services from "./pages/Services.js";
import Webdevelopment from "./pages/Webdevelopment.js";
import Webdesigning from "./pages/Webdesigning.js";
import Appios from "./pages/Appios.js";
import Graphic from "./pages/Graphic.js";
import Digitalmarket from "./pages/Digitalmarket.js";
import Project from "./pages/Project.js";
import Contact from "./pages/Contact.js";

const App = () =>{
  return(
    <Router>
      <Switch>
         <Route exact path="/" component={Home} />   
         <Route exact path="/about" component={Aboutus} /> 
         <Route exact path="/services" component={Services} />  
         <Route exact path="/web-development" component={Webdevelopment} />  
         <Route exact path="/web-designing" component={Webdesigning} />  
         <Route exact path="/app-ios" component={Appios} />  
         <Route exact path="/graphics" component={Graphic} />  
         <Route exact path="/digital-marketing" component={Digitalmarket} />  
         <Route exact path="/project" component={Project} />  
         <Route exact path="/contact" component={Contact} />  
      </Switch>    
    </Router>
  )
}

export default App;